import { useEffect, useState } from "react";
import uuid from "react-uuid";
import "./App.css";
import Navbar from "./components/Navbar";
import Main from "./components/Main";
import Sidebar from "./components/Sidebar";

function App() {
//Use localStorage
  const [notes, setNotes] = useState(
    localStorage.notes ? JSON.parse(localStorage.notes) : []
  );
  const [activeNote, setActiveNote] = useState(false);
//Deactivate Edit Mode on change Notes
  useEffect(() => {setEditMode(false)},[activeNote]);
//Set Notes on change "notes"
  useEffect(() => {
    localStorage.setItem("notes", JSON.stringify(notes));
  }, [notes]);
//Add logic
  const onAddNote = () => {
    const newNote = {
      id: uuid(),
      title: "Untitled Note",
      body: "",
      lastModified: Date.now(),
    };
    setNotes([newNote, ...notes]);
    setActiveNote(newNote.id);
  };
//Modal window logic
  const [open, setOpen] = useState(false);
//Delete logic
  const onDeleteNote = () => {
    setNotes(notes.filter(({ id }) => id !== activeNote));
    setOpen(false);
    setActiveNote(false);
  };
//Update logic
  const onUpdateNote = (updatedNote) => {
    const updatedNotesArr = notes.map((note) => {
      if (note.id === updatedNote.id) {
        return updatedNote;
      }
      return note;
    });
    setNotes(updatedNotesArr);
  };
//Get Active logic
  const getActiveNote = () => {
    return notes.find(({ id }) => id === activeNote);
  };
//Edit Mode logic
  const [editMode, setEditMode] = useState(false);
  const editor = () => {
    if(editMode === true) {setEditMode(false)}
    else {setEditMode(true)}
  }
//Search logic
  const [searchValue, setSearchValue] = useState('');
    const onChangeSearchValue = (e) => {
      setSearchValue(e.target.value);
    }
    useEffect(() => {setActiveNote(false)},[searchValue])
  return (
    <div className="App">
      <Navbar 
      onAddNote={onAddNote}  
      editor={editor}  
      searchValue={searchValue} 
      onChangeSearchValue={onChangeSearchValue}
      onDeleteNote={onDeleteNote} 
      open={open}
      setOpen={setOpen}
      activeNote={activeNote}/>
      <div className="flex-container">
        <Sidebar
          notes={notes}
          activeNote={activeNote}
          setActiveNote={setActiveNote}
          searchValue={searchValue}
        />
        <Main 
        activeNote={getActiveNote()} 
        onUpdateNote={onUpdateNote} 
        editMode={editMode} 
        setEditMode={setEditMode}/>
      </div>
    </div>
  );
}

export default App;