const Sidebar = ({ notes, activeNote, setActiveNote, searchValue }) => {
//Sort logic
  const sortedNotes = notes.sort((a, b) => b.lastModified - a.lastModified);
    return (
      <div className="app-sidebar">
        <div className="app-sidebar-notes">
        {
          notes.filter(obj => {
            const toLower = (obj.title).toLowerCase();
            if(toLower.includes(searchValue.toLowerCase())){
              return true 
            }
          }).map((obj) => (
            <div
              className={`app-sidebar-note ${obj.id === activeNote && "active"}`}
              onClick={() => setActiveNote(obj.id)}
            >
              <div className="sidebar-note-title">
                <strong>{obj.title}</strong>
              </div>
  
              <p>{obj.body && obj.body.substr(0, 100) + "..."}</p>
              <small className="note-meta">
                Last Modified{" "}
                {new Date(obj.lastModified).toLocaleDateString("en-GB", {
                  hour: "2-digit",
                  minute: "2-digit",
                })}
              </small>
            </div>
          ))}
        </div>
      </div>
    );
  };
  
  export default Sidebar;