import { AiOutlineDelete, AiOutlinePlus, AiOutlineEdit } from "react-icons/ai"

const Navbar = ({onAddNote, editor, searchValue, onChangeSearchValue, onDeleteNote, open, setOpen, activeNote}) => {
    return(
        <nav>
            <div className="nav-left">
                <button className="btn" onClick={onAddNote}><AiOutlinePlus /></button>
                <button disabled={!activeNote} className="btn" onClick={() => setOpen(true)}><AiOutlineDelete /></button>
                    <div className={`overlay animated ${open ? 'show' : ''}`}>
                        <div className="modal">
                            <svg onClick={() => setOpen(false)} height="200" viewBox="0 0 200 200" width="200">
                            <title />
                            <path  d="M114,100l49-49a9.9,9.9,0,0,0-14-14L100,86,51,37A9.9,9.9,0,0,0,37,51l49,49L37,149a9.9,9.9,0,0,0,14,14l49-49,49,49a9.9,9.9,0,0,0,14-14Z" />
                            </svg>
                            <p className="modalText">Are you sure you want to delete the note?</p>
                            <button className="btn alt" onClick={onDeleteNote}>Yes, Delete</button>
                        </div>
                    </div>
                <button disabled={!activeNote} className="btn" onClick={editor}><AiOutlineEdit /></button>
            </div>
            <div className="nav-right">
                <input value={searchValue} onChange={onChangeSearchValue} className="nav-search" type="text" placeholder="Search" />
            </div>
        </nav>
    )
}
export default Navbar