import ReactMarkdown from "react-markdown";

const Main = ({ activeNote, onUpdateNote, editMode, setEditMode }) => {
//Update logic
  const onEditField = (field, value) => {
    onUpdateNote({
      ...activeNote,
      [field]: value,
      lastModified: Date.now(),
    });
  };
//If no Active Note
  if (!activeNote) return <div className="no-active-note">No Active Note</div>;

  return (
    <div className="app-main">
      {editMode !== true ? (
        <div className="app-main-note-preview">
          <div className="app-main-last-modified">
                Last Modified{" "}
                {new Date(activeNote.lastModified).toLocaleDateString("en-GB", {
                  hour: "2-digit",
                  minute: "2-digit",
                })}
              </div>
          <h1 className="preview-title">{activeNote.title}</h1>
          <ReactMarkdown className="markdown-preview">
            {activeNote.body}
          </ReactMarkdown>
        </div>
      ) : (
        <div className="app-main-note-edit">
        <input
          type="text"
          id="title"
          placeholder="Note Title"
          value={activeNote.title}
          onChange={(e) => onEditField("title", e.target.value)}
          autoFocus
        />
        <textarea
          id="body"
          placeholder="Write your note here..."
          value={activeNote.body}
          onChange={(e) => onEditField("body", e.target.value)}
        />
        <button className="btn alt" onClick={() => setEditMode(false)}>Done</button>
      </div>
      )
      }
    </div>
  );
};

export default Main;